.social {
  @include list-reset;
  display: flex;
  flex-wrap: wrap;
  margin-left: -18px;
}

.social__item {
  margin-left: 18px;
}

.social__link {
  display: block;
  width: 48px;
  height: 48px;
  border: 3px solid rgba($blue, 0.1);
  border-radius: 50%;
  position: relative;
  transition: 0.1s;

  &::before {
    position: absolute;
    content: "";
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    background-repeat: no-repeat;
    top: 50%;
  }

  &--twitter::before {
    background-image: url("../img/icon-twitter.svg");
    width: 18px;
    height: 14px;
  }

  &--facebook::before {
    background-image: url("../img/icon-facebook.svg");
    width: 9px;
    height: 18px;
  }

  &--youtube::before {
    background-image: url("../img/icon-youtube.svg");
    width: 15px;
    height: 14px;
  }

  &:hover,
  &:focus {
    border: 3px solid $blue;
  }

  &:active::before {
    opacity: 0.3;
  }

  &:active {
    border: 3px solid rgba($blue, 0.1);
  }
}

